/* === Reset & Base === */

* {
  -webkit-tap-highlight-color: transparent;
  -webkit-focus-ring-color: transparent;
  outline: none;
}

* {
  -webkit-user-select: none;
  user-select: none;
}

/* Hide scrollbar while keeping scroll functionality (for Chrome/Edge/Safari) */
body::-webkit-scrollbar {
  display: none;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html,
body {
  width: 100vw;
  min-height: 100vh;
  color: #fff;
  background: linear-gradient(133deg, #110217 0%, #18142d 100%);
  font-family: "Space Grotesk", sans-serif;
  scroll-behavior: smooth;
  overflow-x: hidden;
  scrollbar-color: #d946ef #1e1b2e;
  scrollbar-width: thin;
}

/* Base scrollbar (desktop) */
html,
body {
  scrollbar-color: #6b21a8 #1a0f2e;
  scrollbar-width: thin;
}

/* Hide scrollbar only on mobile */
@media (max-width: 768px) {
  html,
  body {
    scrollbar-width: none; /* Firefox */
    -ms-overflow-style: none; /* IE 10+ */
  }

  /* Chrome, Safari, Edge (Webkit) */
  html::-webkit-scrollbar,
  body::-webkit-scrollbar {
    display: none;
  }
}

/* === Animated Cursor Dot & Ripple === */
.cursor-dot {
  position: fixed;
  top: 0;
  left: 0;
  width: 15px;
  height: 15px;
  background: #b48aff;
  border-radius: 50%;
  pointer-events: none;
  z-index: 9999;
  box-shadow: 0 0 18px #c084fc89, 0 0 44px #a084fc22;
  transition: background 0.18s, box-shadow 0.22s;
  mix-blend-mode: lighten;
  will-change: transform;
}
.cursor-ripple {
  position: fixed;
  pointer-events: none;
  width: 38px;
  height: 38px;
  border-radius: 50%;
  background: rgba(192, 132, 252, 0.14);
  border: 2px solid #c084fc6d;
  z-index: 10000;
  opacity: 1;
  animation: cursorRippleAnim 0.42s cubic-bezier(0.22, 1, 0.36, 1);
  left: 0;
  top: 0;
}
@keyframes cursorRippleAnim {
  from {
    opacity: 0.48;
    transform: scale(0.7);
  }
  90% {
    opacity: 0.16;
  }
  to {
    opacity: 0;
    transform: scale(1.5);
  }
}

.aurora-canvas {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none; /* So it doesn't block clicks */
  z-index: -10; /* Pushed far behind */
  opacity: 0.6; /* Optional: control visibility */
  mix-blend-mode: normal; /* Prevent bleeding into gradient */
}

/* === Scroll Progress Bar === */
.progress-bar {
  position: fixed;
  top: 0;
  left: 0;
  height: 3px;
  background: linear-gradient(90deg, #c084fc, #60a5fa 60%, #f472b6 90%);
  width: 0%;
  z-index: 10001;
  transition: width 0.2s ease-out;
  pointer-events: none;
}

/* === Page Structure === */
.pricing-page {
  max-width: 1200px;
  margin: 0 auto;
  padding: 4rem 1.5rem 2.5rem;
  min-height: 100vh;
  position: relative;
  z-index: 1;
}

.pricing-header {
  text-align: center;
  margin-bottom: 3rem;
  animation: fadeInHeader 1s ease forwards;
}
@keyframes fadeInHeader {
  0% {
    opacity: 0;
    transform: translateY(40px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.pricing-header h1 {
  font-size: 2.7rem;
  font-weight: 800;
  line-height: 1.16;
  background: linear-gradient(90deg, #c084fc, #60a5fa, #f472b6);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  background-size: 200%;
  animation: gradientShift 7s ease-in-out infinite alternate;
}
@keyframes gradientShift {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 100% 50%;
  }
}
.pricing-header .highlight {
  color: #9c7aff;
  background: none;
  font-weight: 90;
  font-family: "DM Serif Display", serif;
}

/* Micro Text under Header */
.pricing-header .micro {
  color: #aaa;
  margin-top: 0.8rem;
  transition: color 0.3s ease;
}

.pricing-header .micro:hover {
  color: #fff;
}

.section-heading {
  font-size: 2rem;
  font-weight: 700;
  margin: 3rem 0 1.5rem;
  text-align: center;
  color: #ffeeff;
  letter-spacing: 0.4px;
}

/* === Section Layout === */
.section-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 3.5rem;
  margin-bottom: 3rem;
}
@media (max-width: 1024px) {
  .section-row {
    gap: 1rem;
  }
}
@media (max-width: 768px) {
  .section-row {
    flex-direction: column;
    gap: 1.3rem;
  }
}

/* === Card Layouts === */
.plan-card,
.info-card {
  background: linear-gradient(
    135deg,
    rgba(255, 255, 255, 0.045) 60%,
    rgba(192, 132, 252, 0.11)
  );
  border-radius: 29px;
  padding: 2.2rem 2.2rem 2.2rem 2.35rem;
  border: 1.2px solid rgba(255, 255, 255, 0.07);
  backdrop-filter: blur(18px);
  position: relative;
  overflow: hidden;
  min-height: 600px;
  width: 30%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 0.4rem;
  transition: transform 0.24s cubic-bezier(0.24, 0.65, 0.52, 1),
    box-shadow 0.22s, border 0.2s,
    min-height 0.23s cubic-bezier(0.56, 0.32, 0.18, 0.81),
    height 0.23s cubic-bezier(0.56, 0.32, 0.18, 0.81);
  will-change: transform, box-shadow, min-height, height;
}

@media (max-width: 1024px) {
  .plan-card,
  .info-card {
    width: 48%;
    min-height: 500px;
  }
}
@media (max-width: 768px) {
  .plan-card,
  .info-card {
    width: 100%;
    min-height: 480px;
  }
}

.plan-card:hover,
.plan-card.selected-card,
.info-card.expanded,
.info-card:focus-within {
  transform: translateY(-8px) scale(1.025);
  box-shadow: 0 25px 56px #c084fc38, 0 1.5px 18px #60a5faa8;
  border-color: #c084fc44;
}
.info-card:hover {
  background: linear-gradient(
    135deg,
    rgba(255, 255, 255, 0.13) 48%,
    rgba(192, 132, 252, 0.16) 100%
  );
}

/* === Glass Shimmer/Reflection === */
.glass-reflect {
  pointer-events: none;
  position: absolute;
  top: -37%;
  left: -26%;
  width: 145%;
  height: 60px;
  background: linear-gradient(
    110deg,
    rgba(255, 255, 255, 0.15) 6%,
    rgba(255, 255, 255, 0.01) 50%
  );
  filter: blur(3px);
  transform: skewY(-12deg);
  z-index: 2;
  opacity: 0.86;
  animation: glassMove 6.2s cubic-bezier(0.22, 1, 0.36, 1) infinite alternate;
}
@keyframes glassMove {
  0% {
    left: -25%;
  }
  100% {
    left: 100%;
  }
}

/* === Plan Card Content === */
.plan-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #eef;
  margin-bottom: 1.18rem;
}
.plan-divider {
  width: 100%;
  height: 1.1px;
  background: rgba(255, 255, 255, 0.17);
  margin: 1.12rem 0 1.14rem 0;
}
.plan-price {
  font-weight: 800;
  margin-bottom: 1.25rem;
  color: white;
  line-height: 1;
  text-align: left;
}
.price-amount {
  font-size: 3.7rem;
  font-weight: 900;
  display: inline-block;
  margin-right: 0.18em;
  background: linear-gradient(90deg, #c084fc 64%, #60a5fa 100%);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}
.price-period {
  font-size: 1rem;
  color: #aaa;
}

.included-title {
  margin-top: 2rem;
  font-size: 1.07rem;
  font-weight: 700;
  color: #aca2b8;
}

.plan-list {
  list-style: none;
  padding-left: 0;
  margin-top: 1.03rem;
}
.animated-bullet {
  font-size: 0.98rem;
  color: #fff;
}
.animated-bullet .bullet-check {
  font-weight: bold;
  margin-right: 8px;
  color: #c084fc;
  display: inline-block;
  filter: drop-shadow(0 2px 4px #7a00f568);
}
.plan-list li {
  border-radius: 11px;
  padding: 6px 7px 6px 0;
  transition: background 0.2s, color 0.27s;
}

/* === Recommended Ribbon === */
.recommended-ribbon {
  position: absolute;
  top: 45px;
  right: -50px;
  transform: rotate(45deg);
  background: linear-gradient(90deg, #c084fc 0%, #f472b6 100%);
  color: white;
  font-size: 0.85rem;
  font-weight: bold;
  padding: 4.5px 54px;
  border-radius: 5px;
  letter-spacing: 0.6px;
  z-index: 10;
  pointer-events: none;
  box-shadow: 0 0 0 #f472b633;
  animation: ribbonGlow 4.2s ease-in-out infinite alternate;
}
@keyframes ribbonGlow {
  0% {
    box-shadow: 0 0 0 #f472b633;
  }
  90% {
    box-shadow: 0 0 15px #c084fc66;
  }
  100% {
    box-shadow: 0 0 0 #f472b633;
  }
}

/* === Ripple BUTTON === */
.glass-button {
  background: rgba(255, 255, 255, 0.06);
  border: 1.3px solid rgba(255, 255, 255, 0.2);
  padding: 1.08rem 2rem;
  border-radius: 16px;
  color: white;
  font-weight: 600;
  font-size: 1.08rem;
  width: 100%;
  text-align: center;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  backdrop-filter: blur(18px);
  transition: 0.29s;
  z-index: 1;
  outline: none !important;
}
.glass-button:hover,
.glass-button:focus-visible {
  background: linear-gradient(90deg, #c084fc 0%, #60a5fa 100%);
  color: #fff;
  box-shadow: 0 2px 22px #c084fc33;
  filter: brightness(1.03) contrast(1.08);
  outline: none;
}
.glass-button:active {
  background: #c084fc19;
  color: #fff;
}

.ripple {
  position: absolute;
  background: rgba(192, 132, 252, 0.17);
  border-radius: 50%;
  pointer-events: none;
  width: 120px;
  height: 120px;
  transform: translate(-50%, -50%) scale(0);
  left: 50%;
  top: 50%;
  animation: rippleFade 0.47s cubic-bezier(0.29, 0.91, 0.48, 0.97);
  z-index: 0;
}
@keyframes rippleFade {
  to {
    transform: translate(-50%, -50%) scale(2.7);
    opacity: 0;
  }
}
.get-started-container {
  width: 100%;
  display: flex;
  justify-content: center;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 17px;
  padding: 0.54rem 0.3rem 0.3rem;
  margin-top: 1.2rem;
  border: 1px solid rgba(255, 255, 255, 0.09);
}

/* === Info Card Bulleted Lists === */
.bullet-points {
  list-style: none;
  padding-left: 0;
  margin-top: 0.5rem;
  /* animated heights managed by framer-motion, but smooth transition for fallback */
  transition: max-height 0.25s cubic-bezier(0.76, 0.04, 0.28, 0.92),
    opacity 0.17s;
}
.bullet-points li {
  position: relative;
  padding-left: 1.45rem;
  font-size: 0.99rem;
  color: #ddcafd;
  line-height: 1.7;
  margin-bottom: 0.1rem;
  will-change: transform, opacity;
  background: transparent;
  border-radius: 8px;
  transition: color 0.16s, background 0.22s, transform 0.15s;
}
.info-card .bullet-points li:hover,
.info-card .bullet-points li:focus {
  background: #fff !important;
  color: #18142d !important;
  transition: background 0.21s, color 0.15s;
}
.info-card .bullet-points li:hover .bullet-check,
.info-card .bullet-points li:focus .bullet-check {
  color: #18142d !important;
  filter: none;
}
.bullet-points li .bullet-check {
  position: absolute;
  left: 0;
  color: #9a80dc;
  font-weight: bold;
  font-size: 1.13em;
  transition: color 0.18s;
}

.desktop-points {
  display: block;
}

@media (max-width: 1024px) {
  .desktop-points {
    display: none;
  }
}

/* === Collapse Button (Show More/Less) for Info Cards === */
.collapse-btn {
  margin-top: 0.86rem;
  background: rgba(255, 255, 255, 0.07);
  color: #c084fc;
  border: 1px solid rgba(255, 255, 255, 0.11);
  padding: 0.53rem 1.07rem;
  border-radius: 10px;
  font-size: 0.94rem;
  font-weight: 600;
  cursor: pointer;
  transition: 0.24s;
}
.collapse-btn:hover {
  background: #c084fc18;
  color: #b48aff;
}

/* Lottie Center Block */
.lottie-center-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin: 1.8rem 0 1.6rem 0;
}
.lottie-centered {
  width: 170px;
  max-width: 100%;
  margin: 0 auto;
}
@media (max-width: 800px) {
  .lottie-centered {
    width: 120px;
  }
}

.colorful-tagline {
  font-size: 1.19rem;
  font-weight: 700;
  background: linear-gradient(90deg, #ff7f50, #ff00ff, #00ffff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-align: center;
  margin-bottom: 0.84rem;
}
.colorful-tagline .emoji {
  -webkit-text-fill-color: white;
  background: none;
  -webkit-background-clip: initial;
}

/* === POPUP - Growth Slot Compare Popup === */
.compare-popup {
  position: absolute;
  right: 12px;
  top: 22px;
  min-width: 226px;
  background: rgba(44, 12, 68, 0.98);
  color: #fff;
  padding: 15px 20px;
  border-radius: 16px;
  font-size: 1.02rem;
  box-shadow: 0 8px 34px #c084fc33;
  border: 1.2px solid #c084fc4c;
  z-index: 9;
  animation: fadeInHeader 0.3s;
}
.compare-popup b {
  color: #f472b6;
  font-weight: 700;
  font-size: 1.01rem;
}
.compare-popup div {
  font-size: 0.98rem;
  margin-top: 5px;
}

.compare-popup::before {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  border-width: 7px 7px 0 7px;
  border-style: solid;
  border-color: #fff transparent transparent transparent;
}

.info-icon {
  position: relative;
  top: 3.3px; /* try adjusting this between -2px to 4px */
}

@keyframes float {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-5px);
  }
  100% {
    transform: translateY(0px);
  }
}

.compare-popup {
  animation: float 2s ease-in-out infinite;
}

/* === Focus/Selected/Active States === */
.plan-card:focus-within,
.info-card:focus-within,
.glass-button:focus {
  outline: 2px solid #c084fc;
  outline-offset: 4px;
}
.plan-card:active,
.info-card:active,
.glass-button:active {
  transform: scale(0.97);
}

/* === Sticky Checkout Bar === */
.sticky-checkout-bar {
  position: fixed;
  left: 50%;
  bottom: 28px;
  transform: translateX(-50%);
  background: linear-gradient(112deg, #181545 65%, #382c64 100%);
  box-shadow: 0 6px 38px #ad85f6c0, 0 1.5px 8px #60a5fa9c;
  color: #fff;
  padding: 21px 32px;
  border-radius: 32px;
  display: flex;
  align-items: center;
  z-index: 9989;
  font-size: 1.11rem;
  animation: floatCard 2.1s ease-in-out infinite alternate;
  max-width: 98vw;
  width: auto;
  min-width: 0;
  box-sizing: border-box;
  gap: 18px;
  overflow-x: auto;
}

.checkout-button {
  margin-left: 20px;
}

/* ===  Remove left margin on smaller screens === */
@media (max-width: 900px) {
  .checkout-button {
    margin-left: 0;
  }
}

/* === ✅ MOBILE (<=600px) — your exact working version === */
@media (max-width: 600px) {
  .sticky-checkout-bar {
    position: fixed !important;
    bottom: 20px !important;
    left: 50% !important;
    transform: translateX(-50%) !important;
    right: auto !important;
    width: auto;
    max-width: 95vw;
    padding: 14px 5vw;
    border-radius: 18px;
    box-sizing: border-box;
    z-index: 9999;
    overflow-x: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
    text-align: center;
    font-size: 0.95rem;
  }

  .sticky-checkout-bar span {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 90vw;
  }

  .checkout-button {
    padding: 10px 16px;
    font-size: 1rem;
  }
}

/* === ✅ TABLET (601px – 1000px) — mimic mobile behavior === */
@media (min-width: 601px) and (max-width: 1000px) {
  .sticky-checkout-bar {
    position: fixed !important;
    bottom: 24px !important;
    left: 50% !important;
    transform: translateX(-50%) !important;
    right: auto !important;
    width: auto;
    max-width: 90vw;
    padding: 16px 6vw;
    border-radius: 22px;
    box-sizing: border-box;
    z-index: 9999;
    overflow-x: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 12px;
    text-align: center;
    font-size: 1rem;
  }

  .sticky-checkout-bar span {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 85vw;
  }

  .checkout-button {
    padding: 10px 18px;
    font-size: 1.05rem;
  }
}

/* === ✅ Safety positioning override (600px to 1000px) === */
@media (max-width: 1000px) {
  .sticky-checkout-bar {
    left: 50% !important;
    right: auto !important;
    transform: translateX(-50%) !important;
    margin-left: 0 !important;
    margin-right: 0 !important;
  }
}

@keyframes floatCard {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-8px);
  }
}

/* === ✅ Global safety === */
body {
  overflow-x: hidden;
}

/* === Modal Backdrop & Content === */
.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(36, 18, 50, 0.93);
  z-index: 1006;
  display: flex;
  align-items: center;
  justify-content: center;
}
.modal-content {
  background: linear-gradient(133deg, #f7f7ff 0%, #e8d7fd 100%);
  color: #23204c;
  max-width: 395px;
  width: 96vw;
  border-radius: 30px;
  padding: 47px 27px 31px;
  box-shadow: 0 6px 48px #c084fc44;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: shimmerGlow 7s linear infinite alternate;
}
@keyframes shimmerGlow {
  0% {
    box-shadow: 0 6px 38px #c084fc44;
  }
  100% {
    box-shadow: 0 0px 88px #60a5fa44;
  }
}
.modal-content h2 {
  font-size: 2rem;
  margin-bottom: 0.44em;
  color: #7a3cff;
}
.modal-content p {
  font-size: 1.15rem;
  color: #291e55;
}
.modal-content button {
  margin-top: 1.11rem;
}

/* === Footer & Trust === */
.footer-trust {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.08rem;
  gap: 16px;
  margin: 2.1rem 0 0 0;
  color: #fef3ff;
  letter-spacing: 0.1px;
  font-weight: 700;
}
.pricing-footer {
  text-align: center;
  color: #cfcbdd;
  font-size: 1.03rem;
  margin-top: 2.4rem;
  opacity: 0.8;
}

/* === Responsive layout tweaks === */
@media (max-width: 1024px) {
  .plan-card,
  .info-card {
    width: 49%;
    min-height: 370px;
  }
  .footer-trust {
    font-size: 0.99rem;
  }
}
@media (max-width: 800px) {
  .plan-card,
  .info-card {
    width: 100%;
    min-height: 265px;
  }
  .sticky-checkout-bar {
    font-size: 0.95rem;
  }
  .footer-trust {
    margin-bottom: 1rem;
  }
  body {
    cursor: default;
  }
  .cursor-dot,
  .cursor-ripple {
    display: none !important;
  }
}
@media (max-width: 500px) {
  .plan-card,
  .info-card {
    min-height: 120px;
  }
  .footer-trust {
    font-size: 0.95rem;
  }
  .section-heading,
  .centered-heading {
    font-size: 1.2rem;
  }
}

/* === For smoother buttons on touch === */
@media (hover: none) and (pointer: coarse) {
  .glass-button:hover {
    filter: unset;
    background: #c084fc27;
  }
}

/* === payment-success-page === */
.payment-status-bg {
  min-height: 100vh;
  background: inherit;
  display: flex;
  align-items: center;
  justify-content: center;
}

.payment-status-panel {
  backdrop-filter: blur(14px) saturate(135%);
  background: linear-gradient(133deg, #1a0f2ea4 0%, #3b195877 100%);
  border-radius: 1.65rem;
  padding: 3.6rem 2.8rem 2.5rem;
  min-width: 350px;
  max-width: 420px;
  width: 96vw;
  box-shadow: 0 8px 32px #c084fc32, 0 0px 0px #fff0;
  border: 1.66px solid #b48aff44;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  z-index: 20;
}

.status-icon.success {
