.toast-content {
  display: flex;
  align-items: center;
  gap: 14px;
  color: #23204c;
}

.toast-content span {
  font-size: 1.6rem;
}

.toast-content p {
  margin: 0;
  font-size: 0.92rem;
  line-height: 1.5;
  font-weight: 500;
}

/* ===== Responsive Adjustments ===== */
@media (max-width: 640px) {
  .currency-toggle-container {
    margin: 1.2rem auto 1.5rem; /* smaller top/bottom */
    max-width: 320px; /* compact width */
    padding: 0 8px;
  }

  .currency-toggle {
    padding: 8px 12px; /* smaller toggle */
  }

  .live-rate-badge {
    flex-direction: column;
    gap: 6px;
    text-align: center;
  }
}
